:root {
  --clr-primary-1: rgba(36, 5, 62, 1);
  --clr-primary-2: rgba(68, 255, 161, 1);
  --clr-neutral-1: rgba(88, 77, 98, 1);
  --clr-neutral-2: rgba(252, 248, 255, 1);
  --clr-neutral-3: rgba(255, 255, 255, 1);
}

@font-face {
  font-family: "Fraunces";
  src: url("/assets/fonts/fraunces/Fraunces_144pt-SemiBold.ttf") format("TrueType");
  font-weight: 600;
}
@font-face {
  font-family: "Manrope";
  src: url("/assets/fonts/manrope/Manrope-Regular.ttf") format("TrueType");
  font-weight: 400;
}
:root {
  --ff-fraunces: "Fraunces", serif;
  --ff-manrope: "Manrope", sans-serif;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

html {
  overflow-x: hidden;
  overflow-y: visible;
}

body {
  min-height: 100vh;
}

img,
svg,
picture,
video {
  display: block;
  max-width: 100%;
}

input,
textarea,
button,
select {
  font: inherit;
}

:root {
  --fs-large: 3.125rem;
  --fs-medium: 2rem;
  --fs-small: 1.75rem;
  --fs-body: 1rem;
  --lh-large: 1;
  --lh-medium: 1.14;
  --lh-small: 1.25;
  --lh-body: 1.78;
}

@media (min-width: 45rem) {
  :root {
    --fs-large: 3.75rem;
    --fs-medium: 3rem;
  }
}
@media (min-width: 75rem) {
  :root {
    --fs-large: 5rem;
    --fs-medium: 3.5rem;
    --fs-small: 2rem;
    --fs-body: 1.125rem;
  }
}
body {
  font-size: var(--fs-body);
  line-height: var(--lh-body);
  font-family: var(--ff-manrope);
}

.wrapper {
  width: 100%;
  max-width: 69.5rem;
  margin-inline: auto;
  padding-inline: 1rem;
}
@media (min-width: 45rem) {
  .wrapper {
    padding-inline: 2.5rem;
  }
}
@media (min-width: 75rem) {
  .wrapper {
    padding-inline: 0;
  }
}

/* Hiding class, making content visible only to screen readers but not visually */
/* "sr" meaning "screen-reader" */
.sr-only:not(:focus):not(:active) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.button-cta--1 {
  position: relative;
  color: var(--clr-neutral-3);
  font-weight: 700;
  transition: color 200ms ease-in-out;
}
.button-cta--1:hover {
  color: var(--clr-primary-2);
}
.button-cta--1::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 0;
  height: 3px;
  width: 100%;
  background-color: var(--clr-primary-2);
}

.button-cta--2 {
  color: var(--clr-primary-1);
  font-weight: 700;
  display: inline-block;
  padding: 0.8125rem 1.5625rem 1rem;
  background-color: var(--clr-primary-2);
  border: 0.125rem solid var(--clr-primary-2);
  text-decoration: none;
  transition: all 200ms ease-in-out;
}
.button-cta--2:hover {
  color: var(--clr-primary-2);
  background-color: var(--clr-primary-1);
}

.header {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: bottom;
  justify-content: space-between;
  padding-top: 2.3125rem;
  z-index: 2;
}
@media (min-width: 75rem) {
  .header {
    padding-top: 3.61125rem;
  }
}
.header__nav ul {
  list-style: none;
}
.header__button {
  text-decoration: none;
}

.hero {
  position: relative;
  padding-top: 8.125rem;
  padding-bottom: 9.75rem;
  text-align: center;
  background-color: var(--clr-primary-1);
  border-radius: 0% 1% 50% 50%/0% 0% 5% 5%;
}
@media (min-width: 45rem) {
  .hero {
    padding-top: 7.625rem;
    padding-bottom: 12.6875rem;
  }
}
@media (min-width: 75rem) {
  .hero {
    padding-top: 9.125rem;
    padding-bottom: 20.375rem;
  }
}
.hero::after {
  content: "";
  position: absolute;
  inset: 0;
  background-color: var(--clr-neutral-2);
  z-index: -1;
}
.hero__title {
  color: var(--clr-neutral-3);
  font-size: var(--fs-large);
  line-height: var(--lh-large);
  font-family: var(--ff-fraunces);
  margin-bottom: 2.5rem;
}
.hero__title:nth-of-type(1) {
  white-space: pre-wrap;
}
.hero__title:nth-of-type(2) {
  white-space: pre-line;
}
.hero__title span {
  position: relative;
}
.hero__title span::after {
  content: "";
  position: absolute;
  bottom: 3px;
  left: 0;
  height: 3px;
  width: 100%;
  background-color: var(--clr-primary-2);
}
.hero__pattern {
  display: none;
}
@media (min-width: 45rem) {
  .hero__pattern {
    display: block;
    position: absolute;
  }
}
.hero__pattern--1 {
  top: 4.4375rem;
  right: 100%;
  transform: translateX(33%);
}
@media (min-width: 75rem) {
  .hero__pattern--1 {
    top: 7.25rem;
    transform: translateX(58%);
  }
}
.hero__pattern--2 {
  top: 15.625rem;
  left: 100%;
  transform: translateX(-50%);
}
@media (min-width: 75rem) {
  .hero__pattern--2 {
    top: 19.625rem;
    transform: translateX(-72%);
  }
}
.hero__img {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 18.625rem;
}
@media (min-width: 45rem) {
  .hero__img {
    max-width: 32.125rem;
  }
}
@media (min-width: 75rem) {
  .hero__img {
    max-width: 47.9375rem;
    transform: translate(-50%, -60%);
  }
}

.features {
  padding-block: 9.6875rem 4.4375rem;
  background-color: var(--clr-neutral-2);
  border-radius: 0% 1% 50% 50%/0% 0% 5% 5%;
}
@media (min-width: 45rem) {
  .features {
    padding-block: 12.6875rem 6.4375rem;
  }
}
@media (min-width: 75rem) {
  .features {
    padding-block: 15.9375rem 13.5625rem;
  }
}
.features__list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}
@media (min-width: 45rem) {
  .features__list {
    gap: 3rem;
  }
}
@media (min-width: 75rem) {
  .features__list {
    flex-direction: row;
    gap: 1.625rem;
  }
}
.features__item {
  text-align: center;
}
@media (min-width: 45rem) {
  .features__item {
    text-align: left;
    display: flex;
    align-items: center;
    gap: 2rem;
  }
}
@media (min-width: 75rem) {
  .features__item {
    text-align: center;
    flex-direction: column;
    flex: 1;
    gap: 0;
  }
}
.features__counter {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 2.875rem;
  font-size: 1.25rem;
  font-family: var(--ff-fraunces);
  color: var(--clr-primary-1);
  padding: 0.3125rem 1rem;
  border: 1px solid var(--clr-primary-1);
  width: fit-content;
  border-radius: 50%;
  margin-inline: auto;
  margin-bottom: 1.5rem;
}
@media (min-width: 45rem) {
  .features__counter {
    margin-bottom: 0;
  }
}
@media (min-width: 75rem) {
  .features__counter {
    width: 3.5rem;
    height: 3.5rem;
    padding: 0.5rem 1.0625rem;
    margin-bottom: 3.5rem;
    font-size: 1.5rem;
  }
}
.features__title {
  font-family: var(--ff-fraunces);
  font-size: var(--fs-small);
  color: var(--clr-primary-1);
  line-height: var(--lh-small);
  margin-bottom: 1rem;
}
@media (min-width: 75rem) {
  .features__title {
    margin-bottom: 1.75rem;
  }
}
.features__description {
  font-size: var(--fs-body);
  color: var(--clr-neutral-1);
}

.contact {
  padding-block: 6.25rem 4.625rem;
}
@media (min-width: 45rem) {
  .contact {
    padding-bottom: 5rem;
  }
}
.contact__img {
  max-width: 17.5625rem;
  margin-inline: auto;
  margin-bottom: -3.25rem;
  z-index: -1;
}
@media (min-width: 45rem) {
  .contact__img {
    margin-right: 100%;
    margin-bottom: -8.125rem;
  }
}
@media (min-width: 75rem) {
  .contact__img {
    max-width: 29.8125rem;
    margin-bottom: -19rem;
  }
}
.contact__text {
  position: relative;
  background-color: var(--clr-primary-1);
  padding: 2rem;
  text-align: center;
  color: var(--clr-neutral-3);
}
@media (min-width: 45rem) {
  .contact__text {
    padding: 3rem 3.5rem 3.5rem;
    max-width: 32.125rem;
    margin-left: auto;
    text-align: left;
  }
}
@media (min-width: 75rem) {
  .contact__text {
    padding: 4rem;
    max-width: 45.625rem;
  }
}
.contact__title {
  font-family: var(--ff-fraunces);
  font-size: var(--fs-medium);
  line-height: var(--lh-medium);
  margin-bottom: 1rem;
}
@media (min-width: 45rem) {
  .contact__title {
    margin-bottom: 1.5rem;
  }
}
@media (min-width: 75rem) {
  .contact__title {
    margin-bottom: 2rem;
  }
}
.contact__description {
  margin-bottom: 1.5rem;
}
@media (min-width: 75rem) {
  .contact__description {
    margin-bottom: 2rem;
  }
}
.contact__pattern {
  display: none;
}
@media (min-width: 45rem) {
  .contact__pattern {
    display: block;
    position: absolute;
    right: 0.75rem;
    top: 100%;
    transform: translateY(-50%);
  }
}
@media (min-width: 75rem) {
  .contact__pattern {
    right: 4.5rem;
    transform: translateY(-71%);
  }
}

.footer {
  padding-bottom: 3.9375rem;
}
.footer__logo {
  display: block;
  max-width: 5.9375rem;
  margin-bottom: 3.5625rem;
  margin-inline: auto;
}
.footer__list {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.75rem;
}
.footer__item-link:hover svg path {
  transition: fill 200ms ease-in-out;
  fill: var(--clr-primary-1);
}